___CSS_LOADER_EXPORT___.push([module.id, `.set_too_high {
    overflow-y: clipped;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 2.5em;
    min-height: 2.5em;
}


@keyframes loading {
    0% {
        background-position: -200em 0;
    }

    100% {
        background-position: calc(200em + 100%) 0;
    }
}

.loading {
    background: linear-gradient(90deg, #ffffff 25%, #e0e0e0 50%, #ffffff 75%);
    background-size: 200em 100%;
    border-radius: 0.8em;
    animation: loading 1.5s infinite;
}

.hidden {
    visibility: hidden;
}

.rotate_icon {
    transform: rotate(180deg);
}
